<template>
  <section class="wrapper">
    <section class="user-wrapper" flex="main:right cross:center" v-if="userToken">
      <p>昵称:{{ nickName }}</p>
      <p @click="logout">退出</p>
    </section>
    <section class="user-wrapper" flex="main:right cross:center" v-else>
      <p @click="showLoginPopup = true">登录</p>/
      <p @click="showRegPopup = true">注册</p>
    </section>
    <section class="center" v-if="songLyrics">
      <img :src="'http://admin.music.com' + songPicture" width="200" height="200">
    </section>
    <section class="lyrics-wrapper" v-if="songLyrics">
      <pre v-html="songLyrics"></pre>
    </section>
    <login v-if="showLoginPopup" @closeLoginPopup="closeLoginPopup"></login>
    <register v-if="showRegPopup" @closeRegPopup="closeRegPopup"></register>
  </section>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import login from '../../components/login'
import register from '../../components/register'
export default {
  name: 'm1-aside',
  components: {
    login,
    register
  },
  data () {
    return {
      showLoginPopup: false,
      showRegPopup: false
    }
  },
  methods: {
    // 关闭登录弹窗
    closeLoginPopup () {
      this.showLoginPopup = false
    },
    // 关闭注册弹窗
    closeRegPopup () {
      this.showRegPopup = false
    },
    // 退出
    async logout () {
      // eslint-disable-next-line
      const res = await this.$http.post('/user/logout/index', {})
      this.handleSetToken('')
      this.handleSetSongArr([])
      this.$router.push('/home')
    },
    ...mapActions('player/song', [
      'handleSetSongArr', 'handleSetSongIdx'
    ]),
    ...mapActions('player/user', [
      'handleSetToken'
    ])
  },
  computed: {
    ...mapState('player/song', [
      'songLyrics', 'songPicture', 'playStatus'
    ]),
    ...mapState('player/user', [
      'userToken', 'nickName'
    ])
  }
}
</script>

<style lang="scss" scoped>
.wrapper {
  height: 100%;
  overflow: hidden;
  .user-wrapper {
    width: 100%;
    height: 40px;
    color: #fff;
    p {
      font-size: 14px;
      padding: 0 10px;
      cursor: pointer;
      letter-spacing: 1px;
    }
  }
  .lyrics-wrapper {
    margin: 20px 0 0;
    height: calc(100% - 270px);
    overflow: hidden;
    overflow-y: auto;
    pre {
      color: #fff;
      font-size: 12px;
      text-align: center;
    }
  }
  .center {
    text-align: center;
    img {
      border-radius: 50%;
      overflow: hidden;
    }
  }
}
</style>
